@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: 'Josefin Sans', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  --accent: #A8DEFF;
  --background: #07080F;
  --title-font: 'Josefin Sans', sans-serif;
  background: var(--background);
  color: white;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home {
  min-height: 100vh;
}

.home nav {
  height: auto;
  display: flex;
  align-items: center;
  padding: 40px;
}

.home nav .name {
  color: var(--accent);
  font-size: 2rem;
  font-family: var(--title-font);
  line-height: 2.5rem;
  position: relative;
}

.home .works {
  padding: 0 40px;
  color: var(--accent);
}

.home .work-videos {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(320px, auto))[auto-fit];
      grid-template-columns: repeat(auto-fit, minmax(320px, auto));
  grid-gap: 30px;
  padding: 20px 0;
}

.home .work-video .details {
  color: white;
}

.home .thumbnail {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.player {
  height: 50vh;
  padding: 0 40px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  grid-gap: 40px;
  gap: 40px;
}

.player .bigplayer {
  height: 100%;
}

.player .bigplayer > iframe {
  height: 100%;
  width: 50vw;
}

@media only screen and (max-width: 900px) {
  .player {
    height: auto;
    flex-direction: column;
  }
  .player .bigplayer {
    width: 100%;
    height: 30vh;
  }
  .player .bigplayer > iframe {
    width: 100%;
  }
}

.player .video-details {
  width: 100%;
  overflow: scroll;
}

.player .video-details .title {
  font-size: 2rem;
  margin-bottom: 10px;
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.player .video-details .subtitle {
  width: 100%;
  -webkit-filter: brightness(0.6);
          filter: brightness(0.6);
  word-wrap: break-word;
}

.youtubeiframe {
  border: none;
}
